	box-sizing: 'border-box',
} */

h2, h3 {
  color: #111;
	padding: 0;
	margin: 0.5rem;
	padding: 5px;
}

p {
	color: white;
}

.card {
  position: relative;
	/* display: flex; */
	justify-content: center;
  height: 100%;
	width: 100%;
  /* margin: 0.2em; */
}

.card-face {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #111;
	/* margin: 0.5em; */
	text-align: center;
	border-radius: 1em;
	overflow: hidden;
	backface-visibility: hidden;
	transition: transform 200ms ease-in-out;
}
.card.visible .card-back {
  /* visibility: visible; */
  transform: rotateY(180deg);
}

.card.visible .card-front {
  /* visibility: visible; */
  transform: rotateY(0)
}

.card-back img {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.card-front {
  background-color: #ddd;
  /* border-color: #333; */
  border: 0.1em solid #111;
  transform: rotateY(180deg);
}

.grows { transition: all 100ms linear; }
.grows:active { transform: scale(1.1); }